footer {
    position: fixed;
    width: 100%;
    height: .9rem;
    left: 0;
    bottom: 0;
    background: #fff;
    display: flex !important;
    z-index: 100;

    div {
        flex: 1;
        height: 0.9rem;

        a {
            display: block;
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;
            box-sizing: border-box;
            padding: 0.06rem 0;
            text-align: center;
            font-family: "微软雅黑";
            color: #999;
            position: relative;

            i {
                width: 100%;
                height: 0.45rem;
                line-height: 0.45rem;
                font-size: 0.42rem !important;
            }

            span {
                width: 100%;
                height: 0.35rem;
                line-height: 0.35rem;
                font-size: 0.26rem;
            }

            .hot {
                position: absolute;
                top: 0;
                left: 60%;
                width: .25rem;

                display: inline;
                border-radius: 50%;
            }
        }

        .nav-active,
        .router-link-exact-active {
            color: black;
            border-color: black
        }

    }
}

.sup {
    position: absolute !important;
    transform: translateX(-50%) !important;
    transform-origin: 0 center !important;
    top: -21px !important;
    right: 21px !important;
    height: 8px !important;
    width: 8px !important;
    border-radius: 100% !important;
    background: #ff5b05 !important;
    z-index: 10 !important;
}